<template>
  <div id="Category">
    <br />
    <br />
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
     <br />
    <el-table :data="permission_data" border style="width: 100%">
      <el-table-column prop="key" label="ID" width="150"></el-table-column>
      <el-table-column prop="label" label="" width="220"></el-table-column>
      <el-table-column prop="disabled" label=""  width="150" :formatter="formatter"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "Permission",
  data() {
    return {
            permission_data: [
        { key: 1, label: "", disabled: false },
        { key: 2, label: "", disabled: false },
        { key: 3, label: "", disabled: false },
        { key: 4, label: "", disabled: false },
        { key: 5, label: "", disabled: false },
        { key: 6, label: "", disabled: false },
        { key: 7, label: "", disabled: false },
        { key: 8, label: "", disabled: false },
        { key: 9, label: "", disabled: false },
        { key: 10, label: "", disabled: false },
        { key: 11, label: "", disabled: false },
        { key: 12, label: "", disabled: false },
        { key: 13, label: "", disabled: false },
        { key: 14, label: "", disabled: false },
        { key: 15, label: "", disabled: false }
      ]
    };
  },
    props: [
    "prop_is_show",
    "prop_category_id",
    "prop_category_name",
    "prop_category_create_time",
    "prop_category_is_delete"
  ],
  methods: {
    formatter(row, column) {
        if (row.disabled===false) {
          return "";
        } else {
          return "";
        }
      }
  },
  computed: {},
  watch: {},
  created() {},
  beforeCreate() {},
  mounted() {},
  beforeMount() {},
  destroyed() {},
  beforeDestroy() {}
};
</script>
<style lang="">
</style>


